import React, { useState } from 'react';
import { Steps, Divider, Form, Select, Input, Button, message, Result, Typography } from 'antd';
import CustomBreadcrumb from '../../components/CustomBreadcrumb';
import UseIntroduce from '../../components/UseIntroduce';
import { DIV, KeyTag, FormWrapper } from '../../components/Div';

const { Step } = Steps;
const { Option } = Select;
const { Text } = Typography;

const FormStepView = () => {
  const [current, setCurrent] = useState(0);
  const [formData, setFormData] = useState<any>({});

  // 
  const formItemLayout = {
    labelCol: { span: 8 },
    wrapperCol: { span: 8 },
  };

  const Step1Form = () => {
    // 
    const validateUsername = (rule: any, value: any) => {
      // 
      const reg = /^(?:(?:\+|00)86)?1(?:(?:3[\d])|(?:4[5-79])|(?:5[0-35-9])|(?:6[5-7])|(?:7[0-8])|(?:8[\d])|(?:9[189]))\d{8}$/;
      if (!(reg.test(value))) return Promise.reject('');
      return Promise.resolve();
    };
    const renderOpts = () => {
      const option = ['', '', ''];
      return option.map((opt: any) => <Option key={opt} value={opt}>{opt}</Option>);
    };
    const prefixSelector = (
      <Form.Item name="prefix" noStyle>
        <Select style={{ width: 100 }}>
          <Option value=""></Option>
          <Option value=""></Option>
        </Select>
      </Form.Item>
    );
    const onFinish = (e: any) => {
      if (!e.email || !e.note || !e.phone || !e.prefix || !e.recipient) {
        message.error('');
        setCurrent(current);
      } else {
        setFormData(e);
        setCurrent(current + 1);
      }
    };
    return (
      <Form
        {...formItemLayout}
        initialValues={{
          recipient: formData.recipient || null,
          email: formData.email || null,
          note: formData.note || null,
          phone: formData.phone || null,
          prefix: formData.prefix || '',
        }}
        onFinish={onFinish} >
        <Form.Item
          name="recipient"
          label="" rules={[
            {
              required: true,
              message: '',
            },
          ]}>
          <Select>
            {renderOpts()}
          </Select>
        </Form.Item>
        <Form.Item
          name="email"
          label=""
          rules={[
            {
              type: 'email',
              message: '',
            },
            {
              required: true,
              message: '',
            },
          ]}
        >
          <Input placeholder='' />
        </Form.Item>
        <Form.Item
          name="note"
          label=""
          rules={[
            {
              required: true,
              message: '',
            },
          ]}>
          <Input placeholder="" />
        </Form.Item>
        <Form.Item
          name="phone"
          label=""
          required
          rules={[
            {
              required: true,
              message: '',
            },
            {
              validator: validateUsername
            }
          ]}
        >
          <Input placeholder="" addonBefore={prefixSelector} />
        </Form.Item>
        <Button type='primary' style={{ marginLeft: 360 }} htmlType="submit"></Button>
      </Form>
    );
  };
  const Step2Form = () => {
    const [loading, setLoading] = useState(false);
    const handlePre = () => {
      setCurrent(current - 1);
    };
    const handleSubmit = () => {
      setLoading(true);
      setTimeout(() => {
        setLoading(false);
        setCurrent(current + 1);
      }, 2000);
    };
    return (
      <Form {...formItemLayout}>
        <Form.Item label="">{formData.recipient}</Form.Item>
        <Form.Item label="">{formData.email}</Form.Item>
        <Form.Item label="">{formData.note}</Form.Item>
        <Form.Item label="">{formData.prefix}</Form.Item>
        <Form.Item label="">{formData.phone}</Form.Item>
        <>
          <Button style={{ marginLeft: 360 }} onClick={handlePre}></Button>
          <Button type='primary' loading={loading} style={{ marginLeft: 10 }} onClick={handleSubmit} ></Button>
        </>
      </Form>
    );
  };
  const Step3Form = () => {
    return (
      <Result
        status="success"
        title=""
        subTitle=""
        extra={[
          <Button
            type='primary'
            key='console'
            onClick={() => {
              setCurrent(0);
              setFormData({});
            }}>
            
          </Button>,
          <Button key='buy' onClick={() => message.info('')}></Button>
        ]}
      />
    );
  };

  const describe =
    <DIV>
      <DIV> button  htmlType  summit Form  onFinish </DIV>
      <DIV>:</DIV>
      <DIV>1<Text delete><KeyTag></KeyTag></Text></DIV>
      <DIV>2<Text delete></Text></DIV>
      <DIV>3<Text delete> <KeyTag>Option </KeyTag></Text></DIV>
      <DIV>4</DIV>
    </DIV>;
  return (
    <>
      <CustomBreadcrumb arr={['', ' ']} />
      <UseIntroduce describe={describe} />
      <FormWrapper >
        <Steps current={current} style={{ margin: '20px auto', maxWidth: '65rem' }}>
          <Step title="" />
          <Step title="" />
          <Step title="" />
        </Steps>
        <Divider />
        {current === 0 && (
          <Step1Form />
        )}
        {current === 1 && (
          <Step2Form />
        )}
        {
          current === 2 && (
            <Step3Form />
          )
        }
      </FormWrapper>
    </>
  );
};

export default FormStepView;
