import React from 'react';
import UseIntroduce from '../../components/UseIntroduce';
import CustomBreadcrumb from '../../components/CustomBreadcrumb';
import { Typography } from 'antd';
import IconFont from '../../components/IconFont';
import { DIV, KeyTag } from '../../components/Div';
import Done from './Done';

const Home = () => {
  const describe =
    <>
      <DIV>
        <Typography.Link href='https://ant.design/components/overview-cn/' target="_blank"><KeyTag>antd<IconFont type="icon-sousuo" /></KeyTag></Typography.Link><KeyTag></KeyTag>
      </DIV>
      <DIV>1</DIV>
      <DIV>2</DIV>
    </>;

  return (
    <>
      <CustomBreadcrumb arr={['']} />
      <UseIntroduce describe={describe} />

      <Done />
    </>
  );
};

export default Home;
