import Home from "../views/Home";
import CustomerButton from '../views/PublicView/Button/customButton';
import UploadView from "../views/PublicView/Upload";
import Barrage from '../views/PublicView/barrage/barrage'
import Interactive from '../views/PublicView/interactive/interactive'
import Charts from '../views/PublicView/charts'
import Rolling from '../views/PublicView/Rolling/Rolling'
import Typography from '../views/PublicView/Typography'
import FormLayout from "../views/FormView/FormTab";
import FormAddSubtractFields from "../views/FormView/FormAddSubtractFields";
import BaseTab from "../views/Table/BaseTab";
import SearchTable from "../views/Table/SearchTable/SearchTable";
import FormCalculate from "../views/FormView/FormCalculate";
import FormStepView from "../views/FormView/FormStep";
import FormBaseView from "../views/FormView/FormBaseView";
import TransformZoom from '../views/Transform/TransformZoom/TransformZoom'
import TransformRotate from '../views/Transform/TransformRotate/TransformRotate'
import Notes from '../views/Components/Notes'
import AboutUs from "../views/About/AboutUs";

// auth 
//  login  role  content
const routes: any = [
  {
    path: '/home', exact: true, name: '', component: Home, auth: [1], meta: { login: true, content: true, role: true }
  },
  // {
  //   path: '/public/button', exact: true, name: '', component: ButtonView, auth: [1], meta: {
  //     login: true, content: true, role: true
  //   }
  // },
  {
    path: '/public/myCopy',
    exact: true,
    name: '',
    component: CustomerButton,
    auth: [1],
    meta: { login: true, content: true, role: true }
  },
  {
    path: '/public/upload', exact: true, name: '', component: UploadView, auth: [1], meta: {
      login: true, content: true, role: true
    }
  },
  {
    path: '/public/barrage', exact: true, name: '', component: Barrage, auth: [1], meta: {
      login: true, content: true, role: true
    }
  },
  {
    path: '/public/interactive', exact: true, name: '', component: Interactive, auth: [1], meta: {
      login: true, content: true, role: true
    }
  },
  {
    path: '/public/charts', exact: true, name: '', component: Charts, auth: [1], meta: {
      login: true, content: true, role: true
    }
  },
  {
    path: '/public/rolling', exact: true, name: '', component: Rolling, auth: [1], meta: {
      login: true, content: true, role: true
    }
  },
  {
    path: '/public/typography', exact: true, name: '', component: Typography, auth: [1], meta: {
      login: true, content: true, role: true
    }
  },
  {
    path: '/form/tab', exact: true, name: 'form', component: FormLayout, auth: [1], meta: {
      login: true, content: true, role: true
    }
  },
  {
    path: '/form/embedded', exact: true, name: '', component: FormAddSubtractFields, auth: [1], meta: {
      login: true, content: true, role: true
    }
  },
  {
    path: '/form/calculate', exact: true, name: '', component: FormCalculate, auth: [1], meta: {
      login: true, content: true, role: true
    }
  },
  {
    path: '/form/step', exact: true, name: '', component: FormStepView, auth: [1], meta: {
      login: true, content: true, role: true
    }
  },
  {
    path: '/form/base', exact: true, name: '', component: FormBaseView, auth: [1], meta: {
      login: true, content: true, role: true
    }
  },
  {
    path: '/tabs/tab', exact: true, name: '', component: BaseTab, auth: [1], meta: {
      login: true, content: true, role: true
    }
  },
  {
    path: '/tabs/searchTable', exact: true, name: '', component: SearchTable, auth: [1], meta: {
      login: true, content: true, role: true
    }
  },
  {
    path: '/transform/zoom', exact: true, name: '', component: TransformZoom, auth: [1], meta: {
      login: true, content: true, role: true
    }
  },
  {
    path: '/transform/rotate', exact: true, name: '', component: TransformRotate, auth: [1], meta: {
      login: true, content: true, role: true
    }
  },
  {
    path: '/component/notes', exact: true, name: '', component: Notes, auth: [1], meta: {
      login: true, content: true, role: true
    }
  },
  {
    path: '/about', exact: true, name: '', component: AboutUs, auth: [1], meta: {
      login: true, content: true, role: true
    }
  },
];

export default routes;
