import React, { useState } from 'react';
import CustomBreadcrumb from '../../components/CustomBreadcrumb';
import UseIntroduce from '../../components/UseIntroduce';
import { DIV, FormWrapper } from '../../components/Div';
import { Button, Cascader, Checkbox, DatePicker, Divider, Form, Input, Radio, Rate } from 'antd';


const FormBaseView = () => {
  const [checkValue, setCheckValue] = useState(false);
  const formItemLayout = {
    labelCol: { span: 8 },
    wrapperCol: { span: 8 },
  };
  const hobbyOptions = ['', '', ''];
  const casCaderOptions = [
    {
      value: '',
      label: '',
      children: [
        {
          value: '',
          label: '',
          children: [
            {
              value: '',
              label: '',
            },
          ],
        },
      ],
    },
    {
      value: '',
      label: '',
      disabled: true,
      children: [
        {
          value: '',
          label: '',
          children: [
            {
              value: '',
              label: '',
            },
          ],
        },
      ],
    },
  ];
  // 
  const onCheckValue = (e: any) => {
    setCheckValue(e.target.checked);
  };
  const onFinish = (e: any) => {
    const params = {
      address: e.address,
      birth: e.birth.format('YYYY-MM-DD'),
      confirm: e.confirm,
      email: e.email,
      hobby: e.hobby,
      password: e.password,
      phone: e.phone,
      rate: e.rate,
      reading: checkValue,
      sex: e.sex,
      user: e.user
    };
    console.log('params', params);
  };
  const describe =
    <DIV>
       value setValue  formItem  form 
    </DIV>;
  return (
    <>
      <CustomBreadcrumb arr={['', ' ']} />
      <UseIntroduce describe={describe} />
      <FormWrapper>
        <Divider></Divider>
        <Form {...formItemLayout} onFinish={onFinish}>
          <Form.Item name="user" label="" tooltip="" rules={[
            {
              required: true,
              message: ''
            }
          ]}>
            <Input placeholder="" autoComplete='off' />
          </Form.Item>
          <Form.Item name="sex" label="" rules={[
            {
              required: true,
              message: ''
            }
          ]}>
            <Radio.Group>
              <Radio value="man"></Radio>
              <Radio value="woman"></Radio>
            </Radio.Group>
          </Form.Item>
          <Form.Item name="hobby" label="" rules={[
            {
              required: true,
              message: ''
            }
          ]}>
            <Checkbox.Group options={hobbyOptions} />
          </Form.Item>
          <Form.Item name="birth" label="" rules={[
            {
              required: true,
              message: ''
            }
          ]}>
            <DatePicker style={{ minWidth: 415 }} placeholder="" />
          </Form.Item>
          <Form.Item
            name="email"
            label=""
            rules={[
              {
                type: 'email',
                message: '',
              },
              {
                required: true,
                message: '',
              },
            ]}
          >
            <Input placeholder='' autoComplete='off' />
          </Form.Item>
          <Form.Item
            name="password"
            label=""
            rules={[
              {
                required: true,
                message: '!',
              },
            ]}
            hasFeedback
          >
            <Input.Password placeholder='' />
          </Form.Item>
          <Form.Item
            name="confirm"
            label=""
            dependencies={['password']}
            hasFeedback
            rules={[
              {
                required: true,
                message: '!',
              },
              ({ getFieldValue }) => ({
                validator(_, value) {
                  if (!value || getFieldValue('password') === value) {
                    return Promise.resolve();
                  }
                  return Promise.reject(new Error('!'));
                },
              }),
            ]}
          >
            <Input.Password placeholder='' />
          </Form.Item>
          <Form.Item name="address" label="" rules={[{ required: true, message: "" }]}>
            <Cascader options={casCaderOptions} placeholder='' />
          </Form.Item>
          <Form.Item name="phone" label="" rules={[{ required: true, message: "" }]}>
            <Input placeholder='' autoComplete='off' />
          </Form.Item>
          <Form.Item name="rate" label="" rules={[{ required: true, message: "" }]}>
            <Rate allowHalf />
          </Form.Item>
          <Form.Item name="reading" style={{ marginLeft: 320 }} >
            <Checkbox value={checkValue} onChange={onCheckValue}>
               <a href='https://www.gwdang.com/static_page/privacy_policy.html' target='_blank' rel="noreferrer"></a>
            </Checkbox>
          </Form.Item>
          <Form.Item >
            <Button type='primary' style={{ marginLeft: 320 }} disabled={checkValue ? false : true} htmlType='submit'></Button>
          </Form.Item>
        </Form>
      </FormWrapper>
    </>
  );
};

export default FormBaseView;
