import React, { useState } from 'react';
import { Typography } from 'antd';

const { Paragraph } = Typography;

const ShowOrClose = (props: { label?: any, rows?: number }) => {
  const { label, rows } = props
  const [visible, setVisible] = useState<boolean>(false);  //

  return (
    <div>
      {/* https://github.com/ant-design/ant-design/issues/20639 */}
      <Paragraph
        ellipsis={
          visible ? false
            : {
              rows: rows || 2, // rows  +1  rows = 32
              expandable: true,
              symbol: <span onClick={(e) => {
                e.stopPropagation(); //,
                setVisible(true)
              }}></span>
            }
        }
      >
        {label ?? ''}
        {visible && <Typography.Link onClick={() => setVisible(false)}> </Typography.Link>}
      </Paragraph>
    </div>
  );
}

export default ShowOrClose;
