import React, { useState } from 'react';
import CustomBreadcrumb from '../../../components/CustomBreadcrumb';
import UseIntroduce from '../../../components/UseIntroduce';
import { KeyTag, FormWrapper, DescribeP } from '../../../components/Div';
import { Button, Form, Upload, message } from 'antd';
import { UploadOutlined } from '@ant-design/icons';

const UploadView = () => {
  const [fileList] = useState([]);

  const props = {
    beforeUpload: (file: any) => {
      console.log('file', file);
      const isPNG = file.type === 'image/jpeg';
      if (!isPNG) {
        message.error(`${file.name} is not a png file`);
      }
      return isPNG || Upload.LIST_IGNORE;
    },
    onChange: (info: any) => {
      console.log(111, info.fileList);
    },
  };
  const describe = <div>
    <DescribeP>1<KeyTag></KeyTag></DescribeP>
  </div>;
  return (
    <>
      <CustomBreadcrumb arr={['', '']} />
      <UseIntroduce describe={describe} />
      <a href='https://blog.csdn.net/Alanrnzearn/article/details/112268892' target='_blank' rel="noreferrer"></a>
      <FormWrapper>
        <Form>
          <Form.Item label="" required>
            <Upload {...props} defaultFileList={fileList}>
              <Button type='primary' icon={<UploadOutlined />}></Button>
              <div style={{ marginTop: '10px', color: 'red' }}> </div>
            </Upload>
          </Form.Item>
        </Form>
      </FormWrapper>
    </>
  );
};

export default UploadView;
